import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Paper,
  TextField,
  Button,
  Typography,
  List,
  ListItem,
  ListItemText,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from '@mui/material';

function ChatSidebar({ toggleChatSidebar }) {
  const [prompt, setPrompt] = useState('');
  const [chatHistory, setChatHistory] = useState([]);
  const [userProfile, setUserProfile] = useState('');
  const [profileModalOpen, setProfileModalOpen] = useState(false);

  useEffect(() => {
    axios
      .get('http://localhost:5000/api/emails/get_analysis', { withCredentials: true })
      .then((response) => {
        const profile = response.data.profile;
        if (profile) {
          setUserProfile(profile);
        }
      })
      .catch((error) => {
        console.error('Error fetching user analysis:', error);
      });
  }, []);

  const handleSend = () => {
    if (!prompt.trim()) return;
    const userMessage = { sender: 'User', text: prompt };
    setChatHistory([...chatHistory, userMessage]);

    axios
      .post('http://localhost:5000/api/ai-chat/', { prompt }, { withCredentials: true })
      .then((response) => {
        const aiMessage = { sender: 'AI', text: response.data.response };
        setChatHistory((history) => [...history, aiMessage]);
      })
      .catch((error) => {
        console.error('Error in AI chat:', error);
      });

    setPrompt('');
  };

  const handleAnalyzeUser = () => {
    axios
      .get('http://localhost:5000/api/emails/analyze_user', { withCredentials: true })
      .then((response) => {
        const data = response.data;
        setUserProfile(data.profile);
        alert('User analysis completed and profile generated.');
      })
      .catch((error) => {
        console.error('Error analyzing user:', error);
        alert('Error analyzing user. Please try again.');
      });
  };

  const handleDisplayProfile = () => {
    setProfileModalOpen(true);
  };

  const handleCloseProfileModal = () => {
    setProfileModalOpen(false);
  };

  return (
    <Paper style={{ padding: '10px', height: '100vh', overflowY: 'auto' }}>
      <div
        style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}
      >
        <Typography variant="h6">AI Chat</Typography>
        {/* Button to hide the Chat Sidebar */}
        <Button variant="text" onClick={toggleChatSidebar}>
          Hide
        </Button>
      </div>
      <List>
        {chatHistory.map((msg, index) => (
          <ListItem key={index}>
            <ListItemText primary={`${msg.sender}: ${msg.text}`} />
          </ListItem>
        ))}
      </List>
      <TextField
        label="Ask AI"
        fullWidth
        value={prompt}
        onChange={(e) => setPrompt(e.target.value)}
      />
      <Button
        variant="contained"
        color="primary"
        onClick={handleSend}
        style={{ marginTop: '10px' }}
      >
        Send
      </Button>
      <div style={{ marginTop: '20px' }}>
        <Button
          variant="contained"
          color="secondary"
          onClick={handleAnalyzeUser}
          style={{ marginRight: '10px' }}
        >
          Analyze User
        </Button>
        <Button
          variant="contained"
          color="secondary"
          onClick={handleDisplayProfile}
          disabled={!userProfile}
        >
          Display Profile
        </Button>
      </div>
      <Dialog
        open={profileModalOpen}
        onClose={handleCloseProfileModal}
        fullWidth
        maxWidth="md"
      >
        <DialogTitle>User Profile</DialogTitle>
        <DialogContent>
          <Typography variant="body1" style={{ whiteSpace: 'pre-wrap' }}>
            {userProfile}
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseProfileModal} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </Paper>
  );
}

export default ChatSidebar;
