import React, { useState } from 'react';
import { AppBar, Toolbar, IconButton, Menu, MenuItem, styled } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import ChatIcon from '@mui/icons-material/Chat';

const StyleAppBar = styled(AppBar)({
  background: "#f5f5f5",
  boxShadow: "none"
});

const Header = ({ toggleDrawer, toggleChatSidebar, handleLogout }) => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleProfileClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleSignOut = () => {
    handleLogout();
    handleMenuClose();
  };

  return (
    <StyleAppBar position="static">
      <Toolbar>
        <IconButton edge="start" color="inherit" onClick={toggleDrawer}>
          <MenuIcon color="action" />
        </IconButton>
        <div style={{ flexGrow: 1 }} />
        {/* IconButton to toggle the Chat Sidebar */}
        <IconButton color="inherit" onClick={toggleChatSidebar}>
          <ChatIcon color="action" />
        </IconButton>
        <IconButton color="inherit" onClick={handleProfileClick}>
          <AccountCircleIcon color="action" />
        </IconButton>
        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleMenuClose}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
        >
          <MenuItem onClick={handleSignOut}>Sign Out</MenuItem>
        </Menu>
      </Toolbar>
    </StyleAppBar>
  );
};

export default Header;
