// App.js:
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Box, Typography, Checkbox, List, Button } from '@mui/material';
import { styled } from '@mui/material';
import { Star, StarBorder } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

const EmailWrapper = styled(Box)({
  padding: '10px',
  background: '#f2f6fc',
  display: 'flex',
  alignItems: 'center',
  cursor: 'pointer',
  marginBottom: '5px',
  '&:hover': {
    background: '#e8edf3',
  },
});

const EmailContent = styled(Box)({
  display: 'flex',
  flexDirection: 'column',
  flex: 1,
  overflow: 'hidden',
});

const EmailOrganization = styled(Typography)({
  fontSize: '14px',
  fontWeight: 500,
  whiteSpace: 'nowrap',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
});

const EmailSubject = styled(Typography)({
  fontSize: '13px',
  whiteSpace: 'nowrap',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
});

const EmailContentText = styled(Typography)({
  fontSize: '12px',
  color: '#555',
  whiteSpace: 'nowrap',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
});

const EmailDate = styled(Typography)({
  fontSize: '12px',
  color: '#5F6368',
  marginLeft: '10px',
});

function EmailList({ emailCache, setEmailCache }) {
  const [emails, setEmails] = useState([]);
  const [loading, setLoading] = useState(false);
  const [pageToken, setPageToken] = useState(null);
  const [nextPageToken, setNextPageToken] = useState(null);
  const [currentTab, setCurrentTab] = useState('INBOX');
  const [selectedEmails, setSelectedEmails] = useState([]);
  const [isWatching, setIsWatching] = useState(false);
  const navigate = useNavigate();

  // Transform the raw email object from the API into a UI-friendly format.
  const transformEmail = (email) => {
    const headers = email.payload?.headers || [];
    const getHeader = (name) => {
      const header = headers.find((h) => h.name.toLowerCase() === name.toLowerCase());
      return header ? header.value : '';
    };

    // Extract organization name from the "From" header.
    const rawFrom = getHeader('From');
    let organization;
    if (rawFrom) {
      // If the header is of the form "Organization Name <email@domain.com>", extract the name.
      const match = rawFrom.match(/^(.*)<.*>$/);
      if (match) {
        organization = match[1].trim();
      } else {
        // Otherwise, take the part before the '@'
        organization = rawFrom.split('@')[0];
      }
    } else {
      organization = 'Unknown Organization';
    }

    // Extract subject and content.
    const subject = getHeader('Subject') || '';
    const content = email.snippet || '';

    // Determine the email date.
    const headerDate = getHeader('Date');
    let dateObj = null;
    if (headerDate && !isNaN(new Date(headerDate).getTime())) {
      dateObj = new Date(headerDate);
    } else if (email.internalDate && !isNaN(Number(email.internalDate))) {
      dateObj = new Date(Number(email.internalDate));
    }

    return {
      id: email.id,
      organization,
      subject,
      content,
      date: dateObj, // Date object or null.
      starred: false,
    };
  };

  const fetchEmails = (token = null, label = currentTab) => {
    setLoading(true);
    axios
      .get('http://localhost:5000/api/emails', {
        params: {
          maxResults: 20,
          pageToken: token,
          label: label,
        },
        withCredentials: true,
      })
      .then((response) => {
        const data = response.data;
        const transformedEmails = data.emails.map(transformEmail);
        setEmails(transformedEmails);
        setNextPageToken(data.nextPageToken);
        setPageToken(token);
        // Cache the transformed emails.
        setEmailCache((prevCache) => ({
          ...prevCache,
          [currentTab]: {
            emails: transformedEmails,
            nextPageToken: data.nextPageToken,
            pageToken: token,
          },
        }));
        setLoading(false);
      })
      .catch((error) => {
        console.error('Error fetching emails:', error);
        setLoading(false);
      });
  };

  useEffect(() => {
    if (emailCache[currentTab]) {
      setEmails(emailCache[currentTab].emails);
      setNextPageToken(emailCache[currentTab].nextPageToken);
      setPageToken(emailCache[currentTab].pageToken);
    } else {
      fetchEmails();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentTab]);

  const handleNextPage = () => {
    if (nextPageToken) {
      fetchEmails(nextPageToken);
    }
  };

  const handlePrevPage = () => {
    console.warn('Previous page functionality is not implemented.');
  };

  const handleEmailClick = (emailId) => {
    navigate(`/email/${emailId}`);
  };

  const toggleSelection = (id) => {
    if (selectedEmails.includes(id)) {
      setSelectedEmails(selectedEmails.filter((eid) => eid !== id));
    } else {
      setSelectedEmails([...selectedEmails, id]);
    }
  };

  const toggleStarred = (id) => {
    setEmails(
      emails.map((email) => {
        if (email.id === id) {
          return { ...email, starred: !email.starred };
        }
        return email;
      })
    );
  };

  const reduceEmailContent = (content, maxLength) => {
    if (!content) return '';
    return content.length <= maxLength ? content : content.slice(0, maxLength) + '...';
  };

  // Handler to toggle watch/unwatch functionality
  const handleToggleWatch = () => {
    if (!isWatching) {
      // Start watching by calling the watch endpoint.
      axios
        .post('http://localhost:5000/api/emails/watch', {}, { withCredentials: true })
        .then((response) => {
          console.log('Started watching:', response.data);
          setIsWatching(true);
        })
        .catch((error) => {
          console.error('Error starting watch:', error);
        });
    } else {
      // Stop watching by calling the stop_watch endpoint.
      axios
        .post('http://localhost:5000/api/emails/stop_watch', {}, { withCredentials: true })
        .then((response) => {
          console.log('Stopped watching:', response.data);
          setIsWatching(false);
        })
        .catch((error) => {
          console.error('Error stopping watch:', error);
        });
    }
  };

  return (
    <Box sx={{ padding: '20px' }}>
      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <Typography variant="h5" gutterBottom>
          {currentTab === 'INBOX' ? 'Inbox' : 'Sent'}
        </Typography>
        <Button variant="contained" color="secondary" onClick={handleToggleWatch}>
          {isWatching ? 'Unwatch' : 'Watch'}
        </Button>
      </Box>
      <List>
        {emails.map((email) => (
          <EmailWrapper key={email.id} onClick={() => handleEmailClick(email.id)}>
            <Checkbox
              checked={selectedEmails.includes(email.id)}
              onClick={(e) => {
                e.stopPropagation();
                toggleSelection(email.id);
              }}
            />
            {email.starred ? (
              <Star
                fontSize="small"
                style={{ marginRight: 10, color: '#FFF200' }}
                onClick={(e) => {
                  e.stopPropagation();
                  toggleStarred(email.id);
                }}
              />
            ) : (
              <StarBorder
                fontSize="small"
                style={{ marginRight: 10 }}
                onClick={(e) => {
                  e.stopPropagation();
                  toggleStarred(email.id);
                }}
              />
            )}
            <EmailContent>
              <EmailOrganization>{email.organization}</EmailOrganization>
              <EmailSubject>{email.subject}</EmailSubject>
              <EmailContentText>{reduceEmailContent(email.content, 30)}</EmailContentText>
            </EmailContent>
            <EmailDate>
              {email.date
                ? `${email.date.getDate()} ${email.date.toLocaleString('default', {
                    month: 'long',
                  })}`
                : 'Invalid date'}
            </EmailDate>
          </EmailWrapper>
        ))}
      </List>
      <Box sx={{ marginTop: '20px' }}>
        <Button variant="contained" color="primary" onClick={handlePrevPage} disabled={loading}>
          Previous Page
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={handleNextPage}
          disabled={loading || !nextPageToken}
          sx={{ marginLeft: '10px' }}
        >
          Next Page
        </Button>
      </Box>
    </Box>
  );
}

export default EmailList;
