// /components/CalendarView.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { List, ListItem, ListItemText, Typography } from '@mui/material';

function CalendarView() {
  const [events, setEvents] = useState([]);

  useEffect(() => {
    // Include withCredentials to ensure cookies (session data) are sent
    axios.get('http://localhost:5000/api/calendar/', { withCredentials: true })
      .then(response => {
        console.log("Successfully fetched calendar events:", response.data);
        setEvents(response.data);
      })
      .catch(error => {
        console.error("Error fetching calendar events:", error);
      });
  }, []);

  return (
    <div style={{ padding: '20px' }}>
      <Typography variant="h5">Calendar Events</Typography>
      <List>
        {events.map(event => (
          <ListItem key={event.id} button>
            <ListItemText 
              primary={event.summary} 
              secondary={event.start?.dateTime || event.start?.date || 'No start time'} 
            />
          </ListItem>
        ))}
      </List>
    </div>
  );
}

export default CalendarView;
