import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import EmailList from './components/EmailList';
import EmailDetail from './components/EmailDetail';
import CalendarView from './components/CalendarView';
import ChatSidebar from './components/ChatSidebar';
import WrittenDrafts from './components/WrittenDrafts';
import SideBar from './components/SideBar';
import { supabase } from './supabaseClient';
import Header from './components/Header';
import { Box } from '@mui/material';

function LoginScreen() {
  const handleLogin = async () => {
    const options = {
      redirectTo: 'http://localhost:3000',
      queryParams: {
        access_type: 'offline',
        prompt: 'consent', // always ask for consent
        scope:
          'openid email profile https://www.googleapis.com/auth/gmail.readonly https://www.googleapis.com/auth/gmail.modify https://www.googleapis.com/auth/gmail.send https://www.googleapis.com/auth/calendar',
      },
    };
    const { data, error } = await supabase.auth.signInWithOAuth({
      provider: 'google',
      options,
    });
    if (error) {
      console.error('Error during sign in:', error);
    }
  };

  return (
    <Box sx={{ mt: 10, textAlign: 'center' }}>
      <h1>Welcome to Gmail Clone</h1>
      <button onClick={handleLogin}>Login with Google</button>
    </Box>
  );
}

function MainApp({ handleLogout, emailCache, setEmailCache }) {
  const [openDrawer, setOpenDrawer] = useState(true);
  // State to control the visibility of the Chat Sidebar
  const [chatSidebarOpen, setChatSidebarOpen] = useState(true);

  const toggleDrawer = () => {
    setOpenDrawer((prev) => !prev);
  };

  // Toggle function for the Chat Sidebar
  const toggleChatSidebar = () => {
    setChatSidebarOpen((prev) => !prev);
  };

  return (
    <Box>
      <Header
        toggleDrawer={toggleDrawer}
        toggleChatSidebar={toggleChatSidebar}
        handleLogout={handleLogout}
      />
      <SideBar open={openDrawer} toggleDrawer={toggleDrawer} />
      <Box
        sx={{
          marginLeft: openDrawer ? '250px' : '0',
          transition: 'margin 0.3s',
          padding: '20px',
          marginTop: '64px',
        }}
      >
        <Routes>
          <Route
            path="/"
            element={<EmailList emailCache={emailCache} setEmailCache={setEmailCache} />}
          />
          <Route path="/email/:emailId" element={<EmailDetail />} />
          <Route path="/calendar" element={<CalendarView />} />
          <Route path="/drafts" element={<WrittenDrafts />} />
          <Route path="/bot-summary" element={<div>Bot Summary (Dummy Page)</div>} />
          <Route path="/settings" element={<div>Settings (Dummy Page)</div>} />
        </Routes>
      </Box>
      {/* Conditionally render ChatSidebar */}
      {chatSidebarOpen && (
        <Box
          sx={{
            position: 'fixed',
            right: 0,
            top: '64px',
            width: '300px',
            height: 'calc(100vh - 64px)',
          }}
        >
          <ChatSidebar toggleChatSidebar={toggleChatSidebar} />
        </Box>
      )}
    </Box>
  );
}

function App() {
  const [user, setUser] = useState(null);
  // Lift emailCache state to persist emails across component mounts.
  const [emailCache, setEmailCache] = useState({});

  useEffect(() => {
    const getSession = async () => {
      const {
        data: { session },
      } = await supabase.auth.getSession();
      if (session) {
        setUser({ email: session.user.email });
        if (session.provider_token) {
          try {
            await fetch('http://localhost:5000/auth/set-token', {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                access_token: session.provider_token,
                refresh_token: session.refresh_token,
                scope: session.scope,
                id_token: session.id_token,
                email: session.user.email,
              }),
              credentials: 'include',
            });
          } catch (err) {
            console.error('Error setting token on backend:', err);
          }
        }
      }
    };

    getSession();

    const { data: authListener } = supabase.auth.onAuthStateChange((event, session) => {
      if (session) {
        setUser({ email: session.user.email });
      } else {
        setUser(null);
      }
    });

    return () => {
      authListener.subscription.unsubscribe();
    };
  }, []);

  const handleLogout = async () => {
    await supabase.auth.signOut();
    localStorage.removeItem('user');
    setUser(null);
  };

  return (
    <Router>
      {user ? (
        <MainApp
          handleLogout={handleLogout}
          emailCache={emailCache}
          setEmailCache={setEmailCache}
        />
      ) : (
        <Routes>
          <Route path="*" element={<LoginScreen />} />
        </Routes>
      )}
    </Router>
  );
}

export default App;
