import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Box, Typography, Button, TextField, Container } from '@mui/material';
import { styled } from '@mui/material';
import { useParams, useNavigate } from 'react-router-dom';
import { ArrowBack, Delete } from '@mui/icons-material';
import { emptyProfilePic } from '../assets/Assets';

const IconWrapper = styled(Box)({
  padding: 15,
  display: 'flex',
  alignItems: 'center',
});

const Subject = styled(Typography)({
  fontSize: 22,
  margin: '10px 0 20px 75px',
  display: 'flex',
  alignItems: 'center',
});

const Indicator = styled(Box)`
  font-size: 12px;
  background: #ddd;
  color: #222;
  border-radius: 4px;
  margin-left: 6px;
  padding: 2px 4px;
  align-self: center;
`;

const MessageContainer = styled(Box)({
  marginBottom: 16,
  border: '1px solid #e0e0e0',
  borderRadius: 8,
  padding: 16,
  background: '#f2f6fc',
});

const MessageHeader = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  marginBottom: 8,
});

const ProfileImage = styled('img')({
  borderRadius: '50%',
  width: 40,
  height: 40,
  marginRight: 10,
  backgroundColor: '#cccccc',
});

const ContentContainer = styled(Box)({
  flex: 1,
});

const DateText = styled(Typography)({
  marginLeft: 'auto',
  fontSize: 12,
  color: '#5E5E5E',
});

const ReplyContainer = styled(Box)({
  marginTop: 20,
  padding: 16,
  border: '1px solid #e0e0e0',
  borderRadius: 8,
});

function EmailDetail() {
  const { emailId } = useParams();
  const [thread, setThread] = useState(null);
  const [loading, setLoading] = useState(false);
  const [replyVisible, setReplyVisible] = useState(false);
  const [replyContent, setReplyContent] = useState('');
  const [sendingReply, setSendingReply] = useState(false);
  const [showAllMessages, setShowAllMessages] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    setLoading(true);
    axios
      .get(`http://localhost:5000/api/emails/${emailId}`, { withCredentials: true })
      .then((response) => {
        setThread(response.data);
        setLoading(false);
      })
      .catch((error) => {
        console.error('Error fetching email thread:', error);
        setLoading(false);
      });
  }, [emailId]);

  // Decode a base64url encoded string.
  const decodeBase64 = (encodedStr) => {
    const base64 = encodedStr.replace(/-/g, '+').replace(/_/g, '/');
    try {
      return decodeURIComponent(escape(window.atob(base64)));
    } catch (e) {
      return window.atob(base64);
    }
  };

  // Recursively extract the plain text body from the payload.
  const extractBody = (payload) => {
    if (payload.body && payload.body.data) {
      return decodeBase64(payload.body.data);
    }
    if (payload.parts) {
      for (let part of payload.parts) {
        if (part.mimeType === 'text/plain' && part.body && part.body.data) {
          return decodeBase64(part.body.data);
        }
        if (part.parts) {
          const result = extractBody(part);
          if (result) return result;
        }
      }
    }
    return '';
  };

  // Get a header’s value by name.
  const getHeader = (headers, name) => {
    const header = headers.find((h) => h.name.toLowerCase() === name.toLowerCase());
    return header ? header.value : '';
  };

  const handleReplyClick = () => {
    setReplyVisible(true);
  };

  const handleReplyCancel = () => {
    setReplyVisible(false);
    setReplyContent('');
  };

  const handleReplySubmit = () => {
    if (!replyContent.trim()) return;
    setSendingReply(true);
    const messages = thread.messages;
    const latestMessage = messages[messages.length - 1];
    const headers = latestMessage.payload.headers;
    const from = getHeader(headers, 'From');
    let subject = getHeader(headers, 'Subject');
    if (!subject.toLowerCase().startsWith('re:')) {
      subject = 'Re: ' + subject;
    }
    axios
      .post(
        'http://localhost:5000/api/emails/send',
        {
          to: from,
          subject: subject,
          body: replyContent,
        },
        { withCredentials: true }
      )
      .then((response) => {
        setSendingReply(false);
        setReplyVisible(false);
        setReplyContent('');
        console.log('Reply sent successfully:', response.data);
      })
      .catch((error) => {
        console.error('Error sending reply:', error);
        setSendingReply(false);
      });
  };

  const handleBack = () => {
    navigate(-1);
  };

  const handleDelete = () => {
    axios
      .delete(`http://localhost:5000/api/emails/${emailId}`, { withCredentials: true })
      .then((response) => {
        console.log('Email deleted:', response.data);
        navigate(-1);
      })
      .catch((error) => {
        console.error('Error deleting email:', error);
      });
  };

  // Render the message body as HTML if it appears to be HTML.
  const renderMessageBody = (body) => {
    const trimmedBody = body.trim();
    const isHTML =
      trimmedBody.startsWith('<!DOCTYPE') ||
      trimmedBody.startsWith('<html') ||
      trimmedBody.startsWith('<body');
    if (isHTML) {
      return <div dangerouslySetInnerHTML={{ __html: body }} style={{ marginTop: '10px' }} />;
    }
    return (
      <Typography variant="body1" style={{ marginTop: '10px', whiteSpace: 'pre-wrap' }}>
        {body}
      </Typography>
    );
  };

  if (loading) {
    return <Typography>Loading email...</Typography>;
  }

  if (!thread) {
    return <Typography>No email thread found.</Typography>;
  }

  // Show only the latest message by default if there are several.
  const messagesToDisplay =
    !showAllMessages && thread.messages.length > 1
      ? [thread.messages[thread.messages.length - 1]]
      : thread.messages;

  // Use the first message’s subject for the main subject header.
  const firstMessageHeaders = thread.messages[0].payload.headers;
  const mainSubject = getHeader(firstMessageHeaders, 'Subject') || 'No subject';

  return (
    <Container sx={{ py: 2 }}>
      <IconWrapper>
        <ArrowBack fontSize="small" color="action" onClick={handleBack} />
        <Delete fontSize="small" color="action" sx={{ marginLeft: 40 }} onClick={handleDelete} />
      </IconWrapper>
      <Subject>
        {mainSubject}
        <Indicator>Inbox</Indicator>
      </Subject>
      {!showAllMessages && thread.messages.length > 1 && (
        <Button variant="text" onClick={() => setShowAllMessages(true)} sx={{ mb: 2 }}>
          Show previous messages ({thread.messages.length - 1})
        </Button>
      )}
      {messagesToDisplay.map((message, index) => {
        const headers = message.payload.headers;
        const from = getHeader(headers, 'From');
        const date = getHeader(headers, 'Date');
        const body = extractBody(message.payload);
        return (
          <MessageContainer key={index}>
            <MessageHeader>
              <ProfileImage src={emptyProfilePic} alt="profile" />
              <ContentContainer>
                <Typography variant="subtitle2">
                  {from.split('@')[0]} <Box component="span">&nbsp;&lt;{from}&gt;</Box>
                </Typography>
              </ContentContainer>
              <DateText>
                {new Date(date).getDate()}{' '}
                {new Date(date).toLocaleString('default', { month: 'long' })}{' '}
                {new Date(date).getFullYear()}
              </DateText>
            </MessageHeader>
            {renderMessageBody(body)}
          </MessageContainer>
        );
      })}
      {replyVisible ? (
        <ReplyContainer>
          <Typography variant="h6" gutterBottom>
            Reply
          </Typography>
          <TextField
            label="Your Reply"
            multiline
            rows={6}
            variant="outlined"
            fullWidth
            value={replyContent}
            onChange={(e) => setReplyContent(e.target.value)}
            sx={{ mb: 2 }}
          />
          <Box>
            <Button variant="contained" color="primary" onClick={handleReplySubmit} disabled={sendingReply}>
              {sendingReply ? 'Sending...' : 'Send Reply'}
            </Button>
            <Button variant="text" onClick={handleReplyCancel} sx={{ ml: 2 }}>
              Cancel
            </Button>
          </Box>
        </ReplyContainer>
      ) : (
        <Button variant="contained" color="primary" onClick={handleReplyClick} sx={{ mt: 2 }}>
          Reply
        </Button>
      )}
    </Container>
  );
}

export default EmailDetail;
