// components/WrittenDrafts.js
import React, { useState, useEffect } from 'react';
import { supabase } from '../supabaseClient';
import { Button, Tabs, Tab, List, ListItem, ListItemText, Typography } from '@mui/material';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function WrittenDrafts() {
  const [tab, setTab] = useState(0);
  const [drafts, setDrafts] = useState([]);
  const [infoItems, setInfoItems] = useState([]);
  const [promotions, setPromotions] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    // On mount, fetch user's stored drafts, info, and promotions from Supabase.
    fetchUserData();
  }, []);

  const fetchUserData = async () => {
    // Retrieve the current user using supabase.auth.getUser()
    const { data: { user } } = await supabase.auth.getUser();
    if (user) {
      // Assumes the users table has columns: drafts, information, promotions.
      let { data, error } = await supabase
        .from('users')
        .select('drafts, information, promotions')
        .eq('email', user.email)
        .single();
      if (error) {
        console.error('Error fetching user data:', error);
      } else {
        setDrafts(data?.drafts || []);
        setInfoItems(data?.information || []);
        setPromotions(data?.promotions || []);
      }
    }
  };

  const handleTabChange = (event, newValue) => {
    setTab(newValue);
  };

  // When clicking an item, navigate to the Email Detail page using the emailId.
  const handleItemClick = (item) => {
    navigate(`/email/${item.emailId}`);
  };

  const handleReadEmails = async () => {
    try {
      // Call the endpoint that processes the latest emails.
      const response = await axios.get('http://localhost:5000/api/emails/process_latest', { withCredentials: true });
      console.log('Processed emails:', response.data);
      // Refresh the local Supabase data after processing.
      fetchUserData();
    } catch (error) {
      console.error('Error processing emails:', error);
    }
  };

  const renderTabContent = () => {
    if (tab === 0) {
      // Drafts tab – display drafted replies.
      return (
        <List>
          {drafts.map((draft, index) => (
            <ListItem
              button
              key={index}
              // If a threadId exists, use it; otherwise, use emailId.
              onClick={() => navigate(`/email/${draft.emailId || draft.threadId}`)}
            >
              <ListItemText
                primary={`Draft for thread ${draft.threadId || draft.emailId}`}
                secondary={draft.draft}
              />
            </ListItem>
          ))}
        </List>
      );
    } else if (tab === 1) {
      // Info tab – display informational summaries.
      return (
        <List>
          {infoItems.map((item, index) => (
            <ListItem button key={index} onClick={() => handleItemClick(item)}>
              <ListItemText
                primary={`Email ID: ${item.emailId}`}
                secondary={item.summary}
              />
            </ListItem>
          ))}
        </List>
      );
    } else if (tab === 2) {
      // Promotions tab – display promotional summaries.
      return (
        <List>
          {promotions.map((item, index) => (
            <ListItem button key={index} onClick={() => handleItemClick(item)}>
              <ListItemText
                primary={`Email ID: ${item.emailId}`}
                secondary={item.summary}
              />
            </ListItem>
          ))}
        </List>
      );
    }
  };

  return (
    <div style={{ padding: '20px' }}>
      <Typography variant="h5">Written Drafts</Typography>
      <Button
        variant="contained"
        color="primary"
        onClick={handleReadEmails}
        style={{ marginTop: '10px' }}
      >
        Read Emails
      </Button>
      <Tabs value={tab} onChange={handleTabChange} style={{ marginTop: '20px' }}>
        <Tab label="Drafts" />
        <Tab label="Info" />
        <Tab label="Promotions" />
      </Tabs>
      {renderTabContent()}
    </div>
  );
}

export default WrittenDrafts;
